@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700&display=swap');

$red: #ff3838;


* {
    font-family: 'Nunito', sans-serif;
    margin:0; padding:0;
    box-sizing: border-box;
    outline: none; border:none;
    text-decoration: none;
    text-transform: capitalize;
    transition:all .2s linear;
 
    &::selection{
        background: $red;
        color:#fff;
    }
}
  
html {
    font-size: 62.5%;
    overflow-x: hidden;
    scroll-behavior: smooth;
    scroll-padding-top: 6rem;
}
  
body{
    background:#f7f7f7;
}
  
section{
    padding:2rem 9%;
}

.header{
    position: fixed;
    top:0; left: 0; right:0;
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background:#fff;
    padding:2rem 9%;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
  
    &__logo {
        font-size: 2.5rem;
        font-weight: bolder;
        color:#666;

        & i {
            padding-right: .5rem;
            color: $red;
        }
    }

    &__navbar {

        & a {
            font-size: 2rem;
            margin-left: 2rem;
            color:#666;

            &:hover {
                color: $red;
            }
        }
    }

    #menu-bar {
        font-size: 3rem;
        cursor: pointer;
        color:#666;
        border:.1rem solid #666;
        border-radius: .3rem;
        padding:.5rem 1.5rem;
        display: none;
    }
}

@media(max-width:991px){

    html{
        font-size: 55%;
    }
  
    header{
        padding:2rem;
    }
  
    section{
        padding:2rem;
    } 
}


.home {

    display: flex;
    flex-wrap: wrap;
    gap:1.5rem;
    min-height: 100vh;
    align-items: center;
    background:url(../src/img/home-bg.jpg) no-repeat;
    background-size: cover;
    background-position: center;
    
    &__content {
        flex:1 1 40rem;
        padding-top: 6.5rem;

        & h3 {
            font-size: 5rem;
            color:#333;
        }

        & p {
            font-size: 1.7rem;
            color:#666;
            padding:1rem 0;
        }      
    }

    &__image {
        flex: 1 1 40rem; 
        
        & img {
            width:100%;
            padding:1rem;
            animation:float 3s linear infinite;
        }
    }

    @keyframes float{
        0%, 100%{
            transform: translateY(0rem);
        }
        50%{
            transform: translateY(3rem);
        }
    }
}

.btn{
    display: inline-block;
    padding:.8rem 3rem;
    border:.2rem solid $red;
    color: $red;
    cursor: pointer;
    font-size: 1.7rem;
    border-radius: .5rem;
    position: relative;
    overflow: hidden;
    z-index: 0;
    margin-top: 1rem;

    &::before {
        content: '';
        position: absolute;
        top:0; right: 0;
        width:0%;
        height:100%;
        background: $red;
        transition: .3s linear;
        z-index: -1;
    }

    &:hover {
        background: $red;
        color:#fff;

        &::before {
            width:100%;
            left: 0;
        }
    }
}

@media(max-width:768px) {

    #menu-bar {
      display: block !important;
    }
  
    .header {

        &__navbar{
            position: absolute;
            top:100%; left: 0; right:0;
            background: #f7f7f7;
            border-top: .1rem solid rgba(0,0,0,.1);
            clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);

            &.active {
                clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);          
            }

            & a {
                margin: 1.5rem;
                padding:1.5rem;
                display: block;
                border:.2rem solid rgba(0,0,0,.1);
                border-left:.5rem solid $red;
                background:#fff;
            }
        }
    }
}
  
@media(max-width:450px){
  
    html{
        font-size: 50%;
    }
}  
  
  
